import React, { Component } from 'react';
import {Link,Route} from 'react-router-dom';
import All from './All/All';
import code from '../images/code.png';
import code1 from '../images/code1.png';

export default class Api extends Component {
    render() {
        return (
            <div>
                 <div className='homeheader'>
                    <Link to={'/home/all'} className='homeheaderlink'>主页</Link>
                    <div style={{float:"left",fontSize:14,paddingLeft:10,paddingTop:9}}>
                        /  API
                    </div>
                </div>
                <div>
                    <Route path='/home/all' component={All} />
                </div>
                <div>
                    <div style={{marginLeft:25,marginTop:15,marginRight:25}}>
                        <p style={{marginTop:10,fontSize:16,color:'black'}}>以下 api 路径均以 <a href='https://cnodejs.org/api/v1'>https://cnodejs.org/api/v1</a> 为前缀</p>
                        <p style={{fontSize:16,color:'black'}}>update 2019-03-21: 涉及发帖和发评论的接口都已经下线了，太多人为了测试客户端乱发帖了。</p>

                        <div style={{fontSize:28,fontWeight:'bold',paddingTop:15,color:'black'}}>主题</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:10,color:'black'}}>get /topics 主题首页</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 get 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:6,listStyleType:'disc'}}>page<code className='code1'>Number</code> 页数</li>
                            <li style={{fontSize:14,color:'black',paddingBottom:6,listStyleType:'disc'}}>tab <code className='code1'>String</code> 主题分类。目前有<code className='code1'>ask</code> <code className='code1'>share</code> <code className='code1'>job</code> <code className='code1'>good</code></li>
                            <li style={{fontSize:14,color:'black',paddingBottom:6,listStyleType:'disc'}}>limit <code className='code1'>Number</code> 每一页的主题数量</li>
                            <li style={{fontSize:14,color:'black',paddingBottom:6,listStyleType:'disc'}}>mdrender <code className='code1'>String</code> 当为 <code className='code1'>false</code> 时，不渲染。默认为 <code className='code1'>true</code>，渲染出现的所有 markdown 格式文本。</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>示例：<a href='https://cnodejs.org/api/v1/topics'>/api/v1/topics</a></p>

                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:10,color:'black'}}>get /topic/:id 主题详情</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 get 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>mdrender<code className='code1'>String</code> 当为<code className='code1'>false</code>时，不渲染。默认为<code className='code1'>true</code>，渲染出现的所有 markdown 格式文本。</li>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code> 当需要知道一个主题是否被特定用户收藏以及对应评论是否被特定用户点赞时，才需要带此参数。会影响返回值中的 <code className='code1'>is_collect</code>以及<code className='code1'>replies</code>列表中的<code className='code1'>is_uped</code>值。</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>示例：<a href='https://cnodejs.org/api/v1/topic/5433d5e4e737cbe96dcef312'>/api/v1/topic/5433d5e4e737cbe96dcef312</a></p>
                        
                        <div style={{fontSize:28,fontWeight:'bold',paddingTop:15,color:'black'}}>主题收藏</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:10,color:'black'}}>post /topic_collect/collect 收藏主题</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 post 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code>用户的 accessToken</li>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>topic_id<code className='code1'>String</code>主题的id</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <code className='code2'>
                            &#123;success: true&#125;
                        </code>

                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:17,color:'black'}}>post /topic_collect/de_collect 取消主题</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 post 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code>用户的 accessToken</li>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>topic_id<code className='code1'>String</code>主题的id</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <code className='code2'>
                            &#123;success: true&#125;
                        </code>

                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:17,color:'black'}}>get /topic_collect/:loginname 用户所收藏的主题</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>示例：<a href='https://cnodejs.org/api/v1/topic_collect/alsotang'>/api/v1/topic_collect/alsotang</a></p>
                        

                        <div style={{fontSize:28,fontWeight:'bold',paddingTop:15,color:'black'}}>用户</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:5,color:'black'}}>get /user/:loginname 用户详情</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>示例：<a href='https://cnodejs.org/api/v1/user/alsotang'>/api/v1/user/alsotang</a></p>
                        
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:5,color:'black'}}>post /accesstoken 验证 accessToken 的正确性</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 post 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code>用户的 accessToken</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>如果成功匹配上用户，返回成功信息。否则 403。</p>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <code className='code2'>
                            &#123;success: true, loginname: req.user.loginname, id: req.user.id, avatar_url: req.user.avatar_url&#125;
                        </code>

                        <div style={{fontSize:28,fontWeight:'bold',paddingTop:32,color:'black'}}>消息通知</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:5,color:'black'}}>get /message/count 获取未读消息数</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 get 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code></li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <code className='code2'>
                            &#123; data: 3 &#125;                                                  
                        </code>

                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:20,color:'black'}}>get /messages 获取已读和未读消息</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 get 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code></li>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>mdrender<code className='code1'>String</code>当为<code className='code1'>false</code>时，不渲染。默认为<code className='code1'>true</code>，渲染出现的所有 markdown 格式文本。</li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <img src={code}/>
                        
                        <div style={{fontSize:18,fontWeight:'bold',paddingTop:20,color:'black'}}>post /message/mark_all 标记全部已读</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>接收 post 参数</p>
                        <ul style={{paddingLeft:24}}>
                            <li style={{fontSize:14,color:'black',paddingBottom:7,listStyleType:'disc'}}>accesstoken<code className='code1'>String</code></li>
                        </ul>
                        <p style={{fontSize:16,color:'black'}}>返回值示例</p>
                        <img src={code1}/>

                        <div style={{fontSize:28,fontWeight:'bold',paddingTop:25,color:'black'}}>知识点</div>
                        <div style={{color:'#eeeeee'}}>-------------------------------------------------------------------------------------------------------------------------------------------------------</div>
                        <p style={{fontSize:16,color:'black'}}>1、如何获取 accessToken？ 用户登录后，在设置页面可以看到自己的 accessToken。 建议各移动端应用使用手机扫码的形式登录，验证使用 <code className='code1'>/accesstoken</code> 接口，登录后长期保存 accessToken。</p>
                    </div>
                </div>
            </div>
        )
    }
}