import React, { Component } from 'react';
import {Link,Route} from 'react-router-dom';
import All from './All/All';

export default class Good extends Component {
    render() {
        return (
            <div>
                <div className='homeheader'>
                    <Link to={'/home/all'} className='homeheaderlink'>首页</Link>
                    <div style={{float:"left",fontSize:14,paddingLeft:10,paddingTop:9}}>
                        /  登录
                    </div>
                </div>
                <div>
                    <Route path='/home/all' component={All} />
                    <Route path='/home/all' component={All} />
                </div>
                <div style={{height:260}}>
                    <form className='form1'>
                        <div style={{marginBottom:6}}>
                            <span style={{paddingRight:20,color:'black'}}>用户名</span><input type='text' name="usernsme" style={{height:35,width:280,border:'1px solid gray',borderRadius:6}}/>
                        </div>
                        <br/>
                        <div style={{marginBottom:6}}>
                            <span style={{paddingRight:20,paddingLeft:14,color:'black'}}>密码</span><input type='text' name="psw" style={{height:35,width:280,border:'1px solid gray',borderRadius:6}}/>
                        </div>
                        <br/>
                        <Link to={'/home/all'} className='anniu'>
                            <button type="button" value="登录" style={{width:140,height:30,backgroundColor:'#0088cc',borderRadius:5,marginLeft:120}}><span style={{color:'white'}}>登录</span></button>
                        </Link>
                    </form>
                </div>
            </div>
        )
    }
}