import React, { Component } from 'react';
import {Link,Route} from 'react-router-dom';
import All from './All/All';
import yunbiaoge from '../images/yunbiaoge.png';
import yunzhihui from '../images/yunzhihui.png';
import ionichina from '../images/ionichina.png';
import offer from '../images/offer.png';
import jiguang from '../images/jiguang.png';
import team from '../images/team.png';
import fault from '../images/fault.png';

export default class About extends Component {
    render() {
        return (
            <div>
                <div className='homeheader'>
                    <Link to={'/home/all'} className='homeheaderlink'>主页</Link>
                    <div style={{float:"left",fontSize:14,paddingLeft:10,paddingTop:9}}>
                        /  关于
                    </div>
                </div>
                <div>
                    <Route path='/home/all' component={All} />
                </div>
                <div style={{height:980}}>
                    <div style={{marginLeft:25,marginTop:15,marginRight:25}}>
                        
                        <h1 style={{fontWeight:'bold'}}>关于</h1>
                        <hr/>
                        <p style={{marginTop:10,fontSize:16,color:'black'}}>CNode 社区为国内最大最具影响力的 Node.js 开源技术社区，致力于 Node.js 的技术研究。</p>
                        <p style={{fontSize:16,color:'black'}}>CNode 社区由一批热爱 Node.js 技术的工程师发起，目前已经吸引了互联网各个公司的专业技术人员加入，我们非常欢迎更多对 Node.js 感兴趣的朋友。</p>
                        <p style={{fontSize:16,color:'black'}}>CNode 的 SLA 保证是，一个9，即 90.000000%。</p>
                        <p style={{fontSize:16,color:'black'}}>社区目前由 <a href='https://cnodejs.org/user/alsotang'>@alsotang </a>在维护，有问题请联系：<a href="https://github.com/alsotang">https://github.com/alsotang</a></p>
                        <p style={{fontSize:16,color:'black'}}>请关注我们的官方微博：<a href='https://weibo.com/login.php'>http://weibo.com/cnodejs</a></p>
                        
                        <h1 style={{fontWeight:'bold',paddingTop:18,paddingBottom:10}}>移动客户端</h1>
                        <p style={{fontSize:16,color:'black'}}>客户端由<a href='https://cnodejs.org/user/soliury'> @soliury</a> 开发维护。</p>
                        <p style={{fontSize:16,color:'black'}}>源码地址：<a href='https://github.com/soliury/noder-react-native'>https://github.com/soliury/noder-react-native</a>  。</p>
                        <p style={{fontSize:16,color:'black'}}>立即体验 CNode 客户端，直接扫描页面右侧二维码。</p>
                        <p style={{fontSize:16,color:'black'}}>另，安卓用户同时可选择：<a href='https://github.com/TakWolf/CNode-Material-Design'>https://github.com/TakWolf/CNode-Material-Design </a>，这是 Java 原生开发的安卓客户端。</p>

                        <h1 style={{fontWeight:'bold',paddingTop:18,paddingBottom:10}}>友情链接</h1>
                        <div style={{height:170}}>
                            <div style={{width:280,float:'left'}}>
                                <a href='https://www.huoban.com'><img src={yunbiaoge} alt='' style={{height:54}}/></a>
                                <a href='https://www.cloudwise.com'><img src={yunzhihui} alt='' style={{height:54}}/></a>
                                <a href='https://www.http://ionichina.com/'><img src={ionichina} alt='' style={{height:54}}/></a>
                            </div>
                            <div style={{width:280,float:'left'}}>
                                <a href='https://cn.100offer.com'><img src={offer} alt='' style={{height:54}}/></a>
                                <a href='https://www.jiguang.cn'><img src={jiguang} alt='' style={{height:54}}/></a>
                            </div>
                            <div style={{width:280,float:'left'}}>
                                <a href='https://www.teambition.com'><img src={team} alt='' style={{height:54}}/></a>
                                <a href='https://segmentfault.com'><img src={fault} alt='' style={{height:54}}/></a>
                            </div>
                        </div>

                        <h1 style={{fontWeight:'bold',paddingTop:18,paddingBottom:10}}>LOGO</h1>
                        <p style={{fontSize:16,color:'black'}}>白底： <a style={{color:'grey'}} href='https://cnodejs.org/public/images/cnodejs.svg'>/public/images/cnodejs.svg</a></p>
                        <p style={{fontSize:16,color:'black'}}>黑底： <a style={{color:'grey'}} href='https://cnodejs.org/public/images/cnodejs_light.svg'>/public/images/cnodejs_light.svg</a></p>
                    </div>
                </div>
            </div>
        )
    }
}