import React, { Component } from 'react'
import {Link,Route,Redirect} from 'react-router-dom';
import Good from './Good/Good';
import All from './All/All';
import Share from './Share/Share';
import Job from './Job/Job';
import Question from './Question/Question';
import Test from './Test/Test';
import AllContent from './All/Content';
import GoodContent from './Good/Content';
import JobContent from './Job/Content';
import QuestionContent from './Question/Content';
import ShareContent from './Share/Content';
import TestContent from './Test/Content';

export default class Home extends Component {
    render() {
        let url = this.props.match.url
        return (
            <div>
                <div className='homeheader'>
                    <Link to='/home/all/1' className='homeheaderlink'>全部</Link>
                    <Link to='/home/good/1' className='homeheaderlink'>精华</Link>
                    <Link to='/home/share/1' className='homeheaderlink'>分享</Link>
                    <Link to='/home/question/1' className='homeheaderlink'>问答</Link>
                    <Link to='/home/job/1' className='homeheaderlink'>招聘</Link>
                    <Link to='/home/test/1' className='homeheaderlink'>客户端测试</Link>
                </div>
                <div>
                    <Route path='/home/all/:page' component={All} />
                    <Route path='/home/good/:page' component={Good} />
                    <Route path='/home/share/:page' component={Share} />
                    <Route path='/home/question/:page' component={Question} />
                    <Route path='/home/job/:page' component={Job} />
                    <Route path='/home/test/:page' component={Test} />

                    <Route path='/home/all:/:page/:idx' component={AllContent} />
                    <Route path='/home/good:/:page/:idx' component={GoodContent} />
                    <Route path='/home/share:/:page/:idx' component={ShareContent} />
                    <Route path='/home/question:/:page/:idx' component={QuestionContent} />
                    <Route path='/home/job:/:page/:idx' component={JobContent} />
                    <Route path='/home/test:/:page/:idx' component={TestContent} />

                    <Redirect from='/*' to='/home/all/1'></Redirect>
                    {/* <Route path='/home/:tab' component={List}/> */}
                </div>
            </div>
        )
    }
}